<template>
    <div>
        <img class="img-fluid pt-5 mt-5 w-25 mx-auto d-block" alt="logo tukarjual.com" src="../../assets/site-logo.webp">
        <h6 class="text-center mb-5">Silahkan masuk ke dalam akun Pian</h6>
        <form @submit="login" @submit.prevent>
            <div class="form-group">
                <div class="col">
                    <input autocomplete="tel" v-model="number_phone" type="text" name="number_phone" placeholder="Masukkan nomor whatsapp" class="form-control form-control-lg bg-light">
                </div>
            </div>
            <div class="form-group">
                <div class="col">
                    <div class="input-group mb-2">
                        <input autocomplete="current-password" name="password" v-model="password" :type="hidden_password ? 'password' : 'text'" placeholder="Masukkan password" class="form-control form-control-lg bg-light border-right-0">
                        <div class="input-group-append">
                            <div class="input-group-text bg-light">
                                <i v-if="hidden_password" @click="hidden_password=false" class="fa fa-eye-slash"></i>
                                <i v-else @click="hidden_password=true" class="fa fa-eye"></i>
                            </div>
                        </div>
                    </div>
                    <router-link class="small" :to="'/account/forgot?from='+$route.name">Lupa Password ?</router-link>
                </div>
            </div>
            <div class="col pt-3">
                <button class="btn btn-lg text-white btn-block tombol">
                    <i v-if="loading" class="fa fa-spinner fa-spin ml-3"></i>
                    <template v-else>Masuk</template>
                </button>
            </div>
        </form>
        <div class="d-flex flex-row-reverse col">
            <p class="mt-3">Baluman beisi akun? <router-link :to="'register?from='+$route.name" class="badge badge-info" style="cursor: pointer">Daftar Disini</router-link></p>
        </div>
        <modal-warning :image="$store.state.url_images+'modal/login-failed.png'" :ids="'login_failed'" :message="{title: 'Login Gagal', deskripsi: 'Pastikan username dan password yang Pian ketik bujur'}"></modal-warning>
    </div>
</template>
<script>
import modalWarning from '@/components/modal/Failed'
import $ from 'jquery'
export default {
    name: "Login",
    data(){
        return {
            number_phone:"",
            password:"",
            hidden_password:true,
            loading: false
        }
    },
    components:{
        modalWarning
    },
    methods:{
        login(){
            this.loading= true
            this.axios.post(this.$store.state.url_api+ 'auth/login', {
                number_phone: this.number_phone,
                password: this.password
            }).then(response => {
                if(!response.data.meta.errors){
                    localStorage.setItem('token', response.data.token);
                    this.$store.dispatch('setIfLogin', true);
                    this.$store.dispatch('loadCart');
                    this.$router.push({name:"HomePage"});
                }
                this.loading=false
            }).catch(() => {
                $('#login_failed').modal('show')
                this.loading=false
            })
        }
    }
}
</script>